.container {
    background-image: url("../images/login/login-back.jpg");
    background-size: 100% 100vh;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
}

.form-container {
    background: rgba(0, 0, 0, 0.5);
    width: 400px;
    min-width: 200px;
    height: 70%;
    min-height: 450px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    margin-top: 500px;
    margin-bottom: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 (0, 0, 0, 0.2);
}

.form-container h2 {
    color: white;
    margin-top: 10px;
}

.form-container form {
    margin-top: 10%;
}

.form-container .data_container p {
    min-width: 300px;
    height: 40px;
    border-radius: 10px;
    outline: none;
    border: 1px solid black;
    padding-left: 20px;
    width: 70%;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    margin-top: 20px;
}

.form-container .data_container {
    margin-top: 40px;
}

.form-container img {
    height: 100px;
    width: 100px;
    border-radius: 50px;
    overflow: hidden;
    margin-top: 30px;
}